import React from "react";
import Form from "./Form";
import Footer from "./footer";
// var userIsLogin = false;

// console.log(currentTime);

function App() {
  var [userIsLogin, setClick] = React.useState(true);
  function isClick() {
    setClick((userIsLogin = true));
  }
  function unclick() {
    setClick((userIsLogin = false));
  }
  console.log();

  return (
    <div className="container">
      {
        // isLogin === true ? <Login /> : <SignUp />
        // currentTime < 12 ? <h1>Why Are You Still Working!!</h1> : null
      }

      <Form isRegistered={userIsLogin} />
      <button className="btn" onClick={isClick}>
        Sign In
      </button>
      <button className="btn" onClick={unclick}>
        Sign Up
      </button>
      <Footer />
    </div>
  );
}

export default App;
