import React from "react";

function Form(props) {
  return (
    <form className="form">
      <h1>{props.isRegistered === true ? "SIGN IN" : "SIGN UP"}</h1>
      <input type="text" placeholder="Username">
        {props.userInput}
      </input>
      <input type="password" placeholder="Password">
        {props.passInput}
      </input>
      {props.isRegistered === false && (
        <input type="password" placeholder="Confirm Password" />
      )}

      <button type="submit">{props.isRegistered ? "Log In" : "Sign Up"}</button>
    </form>
  );
}
export default Form;
